import React from 'react'
import withNav from 'hoc/withNav'
import SiteHeader from './SiteHeader'
import SiteFooter from './SiteFooter'

function AppLayout ({hasHeader = true, headerProps = {}, hasFooter = true, children, isNavOpen, toggleNav}) {
  return (
    <div className="c-app-layout">
      <div className="c-app-layout__wrapper" style={{position: isNavOpen ? 'static' : 'relative'}}>
        {hasHeader ? <SiteHeader {...headerProps} {...{isNavOpen, toggleNav}} /> : null}
        {children}
        {hasFooter ? <SiteFooter /> : null}
      </div>
    </div>
  )
}

AppLayout.propTypes = {
  isNavOpen: React.PropTypes.bool,
  toggleNav: React.PropTypes.func,
  children: React.PropTypes.node,
  hasHeader: React.PropTypes.bool,
  hasFooter: React.PropTypes.bool,
  headerProps: React.PropTypes.object // eslint-disable-line
}

export default withNav(AppLayout)



