export const TOGGLE_NAV = 'TOGGLE_NAV'

export default function nav (state = {
  isOpen: false
}, {type, payload}) {
  switch (type) {
    case TOGGLE_NAV: {
      return {
        isOpen: payload
      }
    }
    default: {
      return state
    }
  }
}



