import React from 'react'
import getDisplayName from 'recompose/getDisplayName'
import {connect} from 'react-redux'
import {TOGGLE_NAV} from 'app/reducers/nav'

function withNav (WrappedComponent) {
  function NavHOC (props) {
    return <WrappedComponent {...props} />
  }

  NavHOC.displayName = `NavHOC(${getDisplayName(WrappedComponent)})`

  NavHOC.propTypes = {
    isNavOpen: React.PropTypes.bool.isRequired,
    toggleNav: React.PropTypes.func.isRequired
  }

  function mapStateToProps (state) {
    return {
      isNavOpen: state.nav.isOpen
    }
  }

  function mapDispatchToProps (dispatch) {
    return {
      toggleNav: value => dispatch({
        type: TOGGLE_NAV,
        payload: value
      })
    }
  }

  return connect(mapStateToProps, mapDispatchToProps)(NavHOC)
}

export default withNav



